.App {
  text-align: center;
  background-color: goldenrod;
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
}

/* .show {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
} */
.show ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  list-style-type: none;
  margin: 0;
}
.show button {
  width: 220px;
  height: 40px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  margin: 20px;
}
.show button:hover {
  cursor: pointer;
  opacity: 0.8;
}
